<template>
    <v-app-bar app v-bind="{ ...commonAttrs, ...$attrs }" v-on="$listeners" class="x-navbar">
        <template v-for="(_, scopedSlotName) in $scopedSlots" #[scopedSlotName]="slotData">
            <slot :name="scopedSlotName" v-bind="slotData" />
        </template>

        <template v-for="(_, slotName) in $slots" #[slotName]>
            <slot :name="slotName" />
        </template>

        <template v-if="!$slots.default && schemaData">
            <v-toolbar-title class="mr-3">
                <slot name="navbar-logo"></slot>
                <x-nav-logo-panel v-if="!$slots['navbar-logo'] && schemaData.logo && schemaData.logo.small" :image="schemaData.logo.small" :hint="schemaData.logo.hint" position="left"></x-nav-logo-panel>
            </v-toolbar-title>

            <!-- LEFT BAR -->
            <v-toolbar-items class="x-left-bar">
                <slot name="navbar-left-first"></slot>

                <template v-for="(item, $index) in schemaData.left">
                    <x-nav-btn v-if="item.type === 'button'" :key="$index" :item="item" :label="item.label" :icon="item.icon" :hint="item.hint" :disabled="item.disabled" :alert-text="item.badge ? item.badge.text : null" :alert-color="item.badge ? item.badge.color : null" :classDevices="item.class"></x-nav-btn>

                    <x-nav-btn v-if="item.type === 'dropdown-items'" :key="$index" :item="item" :items="item.items" ref="dropdownRef" :label="item.label" :icon="item.icon" :hint="item.hint" :disabled="item.disabled" :alert-text="item.badge ? item.badge.text : null" :alert-color="item.badge ? item.badge.color : null" :classDevices="item.class"></x-nav-btn>

                    <x-nav-btn v-if="item.type === 'dropdown-navigation'" :key="$index" :item="item" ref="dropdownRef" :label="item.label" icon="mdi-dots-grid" :hint="item.hint" :disabled="item.disabled" :alert-text="item.badge ? item.badge.text : null" :alert-color="item.badge ? item.badge.color : null" :classDevices="item.class">
                        <x-navigation-panel :modulesPanel="item.modulesPanel" :historyPanel="item.historyPanel" @click="onClick"></x-navigation-panel>
                    </x-nav-btn>

                    <x-nav-btn v-if="item.type === 'dropdown-user'" :key="$index" :item="item" ref="dropdownRef" :label="item.label" :items="item.items" :hint="item.hint" :disabled="item.disabled" :alert-text="item.badge ? item.badge.text : null" :alert-color="item.badge ? item.badge.color : null" left :classDevices="item.class">
                        <template slot="icon">
                            <v-avatar size="35" color="tree">
                                <v-img :src="item.user.avatar"></v-img>
                            </v-avatar>
                        </template>

                        <template slot="menu-panel">
                            <x-nav-user-panel :user="item.user"></x-nav-user-panel>
                        </template>
                    </x-nav-btn>
                </template>

                <slot name="navbar-left-last"></slot>
            </v-toolbar-items>

            <v-spacer></v-spacer>

            <!-- RIGHT BAR -->
            <v-toolbar-items class="x-right-bar">
                <slot name="navbar-right-first"></slot>

                <template v-for="(item, $index) in schemaData.right">
                    <x-nav-btn v-if="item.type === 'button'" :key="$index" :item="item" :label="item.label" :icon="item.icon" :hint="item.hint" :disabled="item.disabled" :alert-text="item.badge ? item.badge.text : null" :alert-color="item.badge ? item.badge.color : null" :classDevices="item.class"></x-nav-btn>

                    <x-nav-btn v-if="item.type === 'dropdown-items'" :key="$index" :item="item" :items="item.items" :label="item.label" ref="dropdownRef" :icon="item.icon" :hint="item.hint" :disabled="item.disabled" :alert-text="item.badge ? item.badge.text : null" :alert-color="item.badge ? item.badge.color : null" left :classDevices="item.class"></x-nav-btn>

                    <x-nav-btn v-if="item.type === 'dropdown-navigation'" :key="$index" :item="item" ref="dropdownRef" :label="item.label" icon="mdi-dots-grid" :hint="item.hint" :disabled="item.disabled" :alert-text="item.badge ? item.badge.text : null" :alert-color="item.badge ? item.badge.color : null" left :classDevices="item.class">
                        <x-navigation-panel :modulesPanel="item.modulesPanel" :historyPanel="item.historyPanel" @click="onClick"></x-navigation-panel>
                    </x-nav-btn>

                    <x-nav-btn v-if="item.type === 'dropdown-user'" :key="$index" :item="item" ref="dropdownRef" :items="item.items" :label="item.label" :hint="item.hint" :disabled="item.disabled" :alert-text="item.badge ? item.badge.text : null" :alert-color="item.badge ? item.badge.color : null" left :classDevices="item.class">
                        <template slot="icon">
                            <v-avatar size="35" color="tree">
                                <v-img :src="item.user.avatar"></v-img>
                            </v-avatar>
                        </template>

                        <template slot="menu-panel">
                            <x-nav-user-panel :user="item.user" @click="onClick"></x-nav-user-panel>
                        </template>
                    </x-nav-btn>
                </template>

                <slot name="navbar-right-last"></slot>
            </v-toolbar-items>
        </template>
    </v-app-bar>
</template>

<script>
import { recursiveMerge } from '../../commons';

export default {
    name: 'NavBar',
    inheritAttrs: false,
    props: {
        schema: {
            type: Object,
        },
    },
    data() {
        return {
            schemaData: {},
        }
    },
    computed: {
        commonAttrs() {
            return {
                color: this.schemaData['color'] || 'primary',
                elevation: '0',
                dark: true,
                dense: true
            }
        },
    },
    methods: {
        onClick() {
            for(let refName in this.$refs) {
                this.$refs[refName].forEach(item => {
                    if (item.$refs['menuRef']) item.$refs['menuRef'].isActive = false;
                });
            }
        },
    },
    watch: {
        schema: {
            deep: true,
            immediate: true,
            handler(newVal) {
                this.schemaData = recursiveMerge(this.schemaData, newVal);
            },
        }
    },
}
</script>

<style scoped>

</style>
